*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  line-height: 1.15;
  font-family: sans-serif; 
  -ms-text-size-adjust: 100%; 
  -ms-overflow-style: scrollbar; 
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); 
}

a {
  background-color: transparent; 
  -webkit-text-decoration-skip: objects; 
}

button {
  border-radius: 0;
  cursor: pointer;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  line-height: inherit;
  font-family: inherit;
  font-size: inherit;
}

button,
input {
  overflow: visible; 
}

button,
select {
  text-transform: none; 
}

button,
html [type='button'], 
[type='reset'],
[type='submit'] {
  -webkit-appearance: button;
}

input {
  -webkit-appearance: none;
}

a::-moz-focus-inner,
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
}

button::-moz-focus-inner,
[type='button']::-moz-focus-inner,
[type='reset']::-moz-focus-inner,
[type='submit']::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

a:focus,
button:focus,
input:focus {
  outline: none;
}

article,
aside,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section {
  display: block;
}

body {
  margin: 0;
  background-color: #fff;
  text-align: left;
}


[tabindex='-1']:focus {
  outline: 0 !important;
}


hr {
  box-sizing: content-box;
  height: 0; 
  overflow: visible; 
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 1rem;
}

p {
  margin: 0;
  margin-bottom: 1rem;
  padding: 0; 
}

abbr[title],
abbr[data-original-title] { 
  border-bottom: 0; 
  cursor: help;
  text-decoration: underline;
  text-decoration: underline dotted;
}

address {
  margin-bottom: 1rem;
  line-height: inherit;
  font-style: normal;
}

ol,
ul,
dl {
  list-style: none;
  margin-top: 0;
  margin-bottom: 1rem;
  padding: 0;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dd {
  margin-bottom: 0.5rem;
  margin-left: 0; 
}

blockquote {
  margin: 0 0 1rem;
}

dfn {
  font-style: italic; 
}

b,
strong {
  font-weight: bolder; 
}


small {
  font-size: 80%; 
}

sub,
sup {
  position: relative;
  vertical-align: baseline;
  line-height: 0;
  font-size: 75%;
}

sub { bottom: -0.25em; }

sup { top: -0.5em; }


a:not( [href] ):not( [tabindex] ) {
  cursor: pointer;
  text-decoration: none;
  color: inherit;
  
  &:hover,
  &:focus {
    text-decoration: underline;
    color: inherit;
  }

  &:focus {
    outline: 0;
  }
}

pre,
code,
kbd,
samp {
  font-family: monospace;
  font-size: 1em;
}

pre {
  
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
  -ms-overflow-style: scrollbar;
}



figure { 
  margin: 0 0 1rem;
}



img {
  border-style: none; 
  vertical-align: middle;
}

svg:not( :root ) {
  overflow: hidden;
}


table {
  border-collapse: collapse;
}

caption {
  caption-side: bottom;
  text-align: left;
}

th {
  text-align: inherit;
}

label {
  display: inline-block;
}


button:focus {
  outline: none;
}

input[type='radio'],
input[type='checkbox'] {
  box-sizing: border-box; 
  padding: 0;
}

input[type='date'],
input[type='time'],
input[type='datetime-local'],
input[type='month'] {
  -webkit-appearance: listbox;
}

textarea {
  overflow: auto;
  resize: vertical;
}

fieldset {
  margin: 0;
  border: 0;
  padding: 0;
  min-width: 0;
}


legend {
  display: block;
  margin-bottom: 0.5rem;
  padding: 0;
  width: 100%;
  max-width: 100%; 
  line-height: inherit;
  white-space: normal; 
  color: inherit;
  font-size: 1.5rem;
}

progress {
  vertical-align: baseline;
}


[type='number']::-webkit-inner-spin-button,
[type='number']::-webkit-outer-spin-button {
  height: auto;
}

[type='search'] {
  -webkit-appearance: none;
  outline-offset: -2px;
}


[type='search']::-webkit-search-cancel-button,
[type='search']::-webkit-search-decoration {
  -webkit-appearance: none;
}


::-webkit-file-upload-button {
  -webkit-appearance: button; 
  font: inherit; 
}

output {
  display: inline-block;
}

summary {
  display: list-item;
  cursor: pointer;
}

template {
  display: none; 
}


[hidden] {
  display: none !important;
}

